<template>
  <div>
    <div class="header">
      <div class="container">
        <a href="/"><img src="../assets/logo.png" alt=""></a>
      </div>
    </div>
    <div class="banner">
      <img src="../assets/banner.png" alt="">
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Header",
  
};
</script>
<style lang="scss">
.header {
  background: #3150a2;
  padding: 11px 0;
}
.banner {
  position: relative;
  img {
    width: 100%;
  }
}
.searchBar {
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 15px 0;
  .el-input__inner {
    border-radius: 0;
  }
  .el-select {
    &.zone,
    &.ticketInfo {
      width: 185px;
      margin-right: 5px;
    }
  }
  .el-input {
    margin-bottom: 5px;
    &.date {
      width: 175px;
      margin-right: 5px;
    }
    &.keyword {
      width: 295px;
      margin-right: 5px;
    }
  }
  .el-button {
    border-radius: 0;
    &.searchBtn {
      width: 135px;
      background: #0960bd;
      color: #fff;
      border-color: #0960bd;
      height: 40px;
      display: inline-block;
      vertical-align: top;
      line-height: 40px;
      padding: 0;
      transition: all 0.3s;
      i {
        margin-left: 15px;
        font-size: 18px;
      }
      &:hover {
        background: #090089;
        border-color: #090089;
      }
    }
  }
  @media (max-width: 1225px) {
    text-align: center;
    .el-select {
      &.zone,
      &.ticketInfo {
        width: 140px;
        margin-right: 0;
      }
    }
    .el-input {
      &.date {
        width: 140px;
        margin-right: 0;
      }
      &.keyword {
        width: 180px;
        margin-right: 0;
      }
    }
    .el-button {
      &.searchBtn {
        width: 120px;
      }
    }
  }
  @media (max-width: 900px) {
    background: rgba(0, 0, 0, 0.1);
    position: relative;
  }
  @media (max-width: 650px) {
    .el-select {
      &.zone,
      &.ticketInfo {
        width: 100%;
      }
    }
    .el-input {
      &.date {
        width: calc(50% - 3px);
        margin-right: 0;
      }
      &.keyword {
        width: 100%;
      }
    }
    .el-button {
      &.searchBtn {
        width: 100%;
      }
    }
  }
}
</style>




