<template>
  <div>
    <div class="header">
      <div class="container">
        <a href="/"><img src="../assets/logo.png" alt=""></a>
      </div>
    </div>
    <div class="banner">
      <img src="../assets/banner.png" alt="">
      <div class="searchBar">
        <div class="container">
          <el-select v-model="zoneValue" placeholder="请选择" class="zone">
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="zone in zoneOptions"
              :key="zone.value"
              :label="zone.label"
              :value="zone.value">
            </el-option>
          </el-select>
          <el-input placeholder="關鍵字" class="keyword" v-model="keyword"></el-input>
          <el-button class="searchBtn" @click="onSearch">搜尋 <i class="el-icon-search"></i></el-button>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="container">
        <div class="result">Showing <span class="blueColor">{{this.searchResult.length}}</span> results by…</div> 
        <div class="list" >
          <div class="unit" v-for="r in getPageRows()">
            <router-link :to="{ path: '/page', query: { infoId:r.Id }} ">
              <div class="img" :style="{ 'background-image': 'url(' + r.Picture1 + ')' }"></div>
              <div class="txt">
                <div class="title blueColor">{{r.Name}}</div>
                <div class="description">{{r.Description}}</div>
                <ul class="detail">
                  <li><i><span class="glyphicon glyphicon-map-marker"></span></i>{{r.Add}}</li>
                  <li><i><span class="glyphicon glyphicon-earphone"></span></i>{{r.Tel}}</li>
                  <li><i><span class="glyphicon glyphicon-time"></span></i>{{r.Opentime}}</li>
                </ul>
              </div>
            </router-link>
          </div>
        </div>
        <div class="page">
          <a ref="pre" href="#" aria-label="Previous" :class="[currentPage &lt;= 1 ? 'forbit_click' : '']" @click="movePage(-1)"><span aria-hidden="true" class="el-icon-arrow-left"></span></a>
          <ul>
            <li v-for="i in pageCount()" :class="[i === currentPage ? 'active' : '']" @click="changePage(i)"><a href="#">{{i}}</a></li>
          </ul>
          <a ref="aft" href="#" aria-label="Next" :class="[currentPage &gt;= pageSize ? 'forbit_click' : '']" @click="movePage(1)"><span aria-hidden="true" class="el-icon-arrow-right"></span></a>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Index",
  data() {
    return {
      keyword: "",
      getList: [],
      searchResult: [],
      recordList:[],
      currentPage: 1,
      pageSize: 10,
      totalPage: '',
      zoneValue:"all",
      zoneList:[],
      zoneOptions: [{
          value: "楠梓區",
          label: "楠梓區"
      }, {
          value: "左營區",
          label: "左營區"
      }, {
          value: "鼓山區",
          label: "鼓山區"
      }, {
          value: "三民區",
          label: "三民區"
      }, {
          value: "苓雅區",
          label: "苓雅區"
      }, {
          value: "新興區",
          label: "新興區"
      }, {
          value: "前金區",
          label: "前金區"
      }, {
          value: "鹽埕區",
          label: "鹽埕區"
      }, {
          value: "前鎮區",
          label: "前鎮區"
      }, {
          value: "旗津區",
          label: "旗津區"
      }, {
          value: "小港區",
          label: "小港區"
      }],
    };
  },
  mounted() {
    this.getItems();
    this.getPageRows();
  },
  methods: {
    onSearch() {
      // let temp = [];
      // this.recordList.forEach(e => {
      //   if (e.Name.includes(this.keyword)) {
      //     temp.push(e)
      //   }
      // });
      // this.searchResult = temp;

      this.searchResult = _(this.recordList)
        .filter((e) =>{
          if(this.keyword == ''){
            return e
          }else{
            return _.includes(e.Name, this.keyword);
          }
        })
        .filter((z) =>{
          if(this.zoneValue == 'all'){
            return z
          }else{
            return z.Zone == this.zoneValue
          }
        })
        .value()
    },
    getItems() {
      axios
        .post(
          `https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97`
        )
        .then(res => {
          // JSON responses are automatically parsed.
          this.getList = res.data;
          this.recordList = res.data.result.records
          this.searchResult = this.recordList
        })
        .catch(e => {
          this.errors.push(e);
        });
    },
    getPageRows () {
      let start = (this.currentPage - 1) * this.pageSize
      let end = start + this.pageSize
      return this.searchResult.slice(start, end)
    },
    pageCount () {
      //ceil 無條件進位
      return Math.ceil(this.searchResult.length / this.pageSize)
    },
    changePage (page) {
      this.currentPage = page
    },
    movePage (i) {
      this.currentPage = this.currentPage + i
      console.log('current:' + this.currentPage)
      this.getPageRows()
      if(this.currentPage < 1){
        this.currentPage = 1
      }else if(this.currentPage > this.pageSize){
        this.currentPage = this.pageSize
      }else if(this.currentPage > this.pageCount()){
        this.currentPage = this.pageCount()
      }
    }
     
  }
};
</script>

<style lang="scss">
.header {
  background: #3150a2;
  padding: 11px 0;
}
.banner {
  position: relative;
  img {
    width: 100%;
  }
}
.searchBar {
  text-align: center;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 15px 0;
  .el-input__inner {
    border-radius: 0;
  }
  .el-select {
    &.zone,
    &.ticketInfo {
      width: 185px;
      margin-right: 5px;
    }
  }
  .el-input {
    margin-bottom: 5px;
    &.infoChecked {
      width: 175px;
      margin-right: 5px;
    }
    &.keyword {
      width: 295px;
      margin-right: 5px;
    }
  }
  .el-button {
    border-radius: 0;
    &.searchBtn {
      width: 100px;
      background: #0960bd;
      color: #fff;
      border-color: #0960bd;
      height: 40px;
      display: inline-block;
      vertical-align: top;
      line-height: 40px;
      padding: 0;
      transition: all 0.3s;
      i {
        margin-left: 15px;
        font-size: 18px;
      }
      &:hover {
        background: #090089;
        border-color: #090089;
      }
    }
  }
  @media (max-width: 1225px) {
    .el-select {
      &.zone,
      &.ticketInfo {
        width: 140px;
        margin-right: 0;
      }
    }
    .el-input {
      &.infoChecked {
        width: 140px;
        margin-right: 0;
      }
      &.keyword {
        width: 180px;
        margin-right: 0;
      }
    }
  }
  @media (max-width: 900px) {
    background: rgba(0, 0, 0, 0.1);
    position: relative;
  }
  @media (max-width: 650px) {
    .el-select {
      &.zone,
      &.ticketInfo {
        width: 100%;
      }
    }
    .el-input {
      &.infoChecked {
        width: calc(50% - 3px);
        margin-right: 0;
      }
      &.keyword {
        width: 100%;
      }
    }
    .el-button {
      &.searchBtn {
        width: 100%;
      }
    }
  }
}
.content {
  margin: 40px 0;
}
.result {
  font-size: 20px;
  margin-bottom: 20px;
}
.unit {
  display: inline-flex;
  background: #ffffff;
  border: 1px solid #c9c9c4;
  box-sizing: border-box;
  width: calc(50% - 20px);
  margin-bottom: 30px;
  vertical-align: top;
  height: 230px;
  &:nth-child(2n + 1) {
    margin-right: 30px;
  }
  a {
    display: flex;
    width: 100%;
    &:hover {
      text-decoration: none;
      .title {
        color: #090089;
      }
    }
    .img {
      flex: auto;
      background-size: cover;
      background-position: center center;
    }
    .txt {
      flex: none;
      width: calc(100% - 251px);
      padding: 24px;
      .title {
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 5px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .description {
        color: #666;
        margin-bottom: 15px;
        overflow : hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        font-size: 14px;
        height: 40px;
      }
      .detail {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 13px;
        li {
          margin-bottom: 5px;
          color: #999999;
          position: relative;
          padding-left: 22px;
          i {
            vertical-align: top;
            width: 18px;
            text-align: center;
            span{
              color:#666;
              position: absolute;
              left: 0;
              top:4px;
            }
          }
        }
      }
    }
  }
  @media (max-width: 1200px) {
    height: auto;
    margin-bottom: 20px;
    width: calc(50% - 13px);
    &:nth-child(2n + 1) {
      margin-right: 20px;
    }
    a {
      display: block;
      .img {
        flex: inherit;
        height: 250px;
      }
      .txt {
        width: 100%;
        flex: inherit;
      }
    }
  }
  @media (max-width: 500px) {
    width: 100%;
  }
}
.page {
  text-align: center;
  margin-top: 50px;
  a{
    color: #666;
    width: 30px;
    height: 30px;
    text-align: center;
    border-radius: 50%;
    margin: 0 3px;
    line-height: 30px;
    display: inline-block;
    &:hover{
      text-decoration: none;
      background: #0960BD;
      color: #fff;
    }
  }
  ul{
    display: inline-block;
    margin: 0;
    padding: 0;
    li{
      list-style:none;
      display: inline-block;
      &.active{
        a{
          background: #0960BD;
          color: #fff;
          width: 30px;
          height: 30px;
          text-align: center;
          border-radius: 50%;
          line-height: 30px;
          text-decoration: none;
        }
      }
      
    }
  }
}
</style>



