<template>
  <div>
    <Header></Header>
    <div class="pageContent">
      <div class="container" v-for="r in resultInfo">
        <div class="breadcrumbBox">
          <i class="glyphicon glyphicon-home"></i>
          <router-link :to="{path: '/'}">首頁</router-link> / 
          <span>{{r.Name}}</span>
        </div>
        <div class="pageInfo" >
          <div class="pageImg">
            <img :src="r.Picture1" alt="">
          </div>
          <div class="pageTxt">
            <div class="title blueColor">{{r.Name}}</div>
            <div class="description">{{r.Toldescribe}}</div>
            <div class="detail">
              <h4>相關資訊</h4>
              <ul>
                <li>電話：{{r.Tel}}</li>
                <li>地址：{{r.Add}}</li>
                <li>開放時間：{{r.Opentime}}</li>
                <li>票價資訊：{{r.Ticketinfo}}</li>
              </ul>
            </div>
          </div>
        </div>
        <div class="back">
          <a href=""  onclick="history.back()">
            <i class="el-icon-arrow-left"></i>Back
          </a>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from "axios";
import Header from '@/components/header.vue'
export default {
  name: 'page',
  components: {
    Header
  },
  data() {
    return {
      getList: [],
      recordList: [],
      resultInfo:[],
    };
  },
  mounted() {
    this.getItems();
   
    
    // _.filter(this.recordList, {Id: this.$route.query.infoId})
    // console.log(this.$route.query.infoId)
  },
  methods: {
    getItems() {
      axios
        .post(
          `https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97`
        )
        .then(res => {
          // JSON responses are automatically parsed.
          this.getList = res.data;
          this.recordList = res.data.result.records
          this.resultInfo = _.filter(this.recordList, {Id: this.$route.query.infoId})
        })
        .catch(e => {
          this.errors.push(e);
        });
    },
    
  }
}
</script>

<style lang="scss">
  .pageContent{
    > .container{
      background: #fff;
      padding: 50px;
      .pageTxt{
        .title{
          font-size:28px;
          font-weight: bold;
          margin-bottom: 20px;
          
        }
        .description{
          line-height: 25px;
        }
        .detail{
          background: #f8f9fc;
          padding: 30px;
          margin-top: 30px;
          h4{
            color:#666;
            border-bottom:1px solid #ccc;
            padding: 0 0 15px;
            margin: 0 0 15px;
            position: relative;
            &::after{
              content:"";
              position: absolute;
              width:75px;
              background: #0060ca;
              left: 0;
              bottom:-1px;
              height: 1px;
            }
          }
          ul{
            margin: 0;
            padding: 0;
            list-style: none;
            li{
              display: block;
              vertical-align: top;
              color: #999;
              margin-right: 40px;
              margin-bottom: 10px;
              i{
                margin-right: 5px;
                display: inline-block;
                vertical-align: top;
                width:18px;
                text-align: center;
              }
            }
          }
        }
      }
    }
  }
  .breadcrumbBox{
    font-size: 20px;
    margin-bottom: 30px;
    a, span{
      display: inline-block;
      vertical-align: middle;
      margin-right: 5px;
    }
    i{
      position: relative;
      top: 4px;
      color: #333;
      margin-right: 5px;
    }
    a{
      color: #0960BD;
      &:hover{
        color: #0056b3;
        text-decoration: none;
        font-weight: bold;
      }
    }
  }
  .pageInfo{
    display: flex;
    .pageImg{
      flex:none;
      width:360px;
      img{
        width: 100%;
        box-shadow: 2px 2px 3px #d2cfcf;
      }
    }
    .pageTxt{
      flex:auto;
      padding-left: 60px;
    }
    @media (max-width: 1000px){
      display: block;
      .pageImg{
        flex: inherit;
        margin-bottom: 20px;
        width:100%;
      }
      .pageTxt{
        padding-left: 0;
      }
    }
  }
  .back{
    a{
      display: block;
      background: #0060CA;
      border-radius: 39px;
      width: 100px;
      margin: 0 auto;
      text-align: center;
      color: #fff;
      padding: 5px 10px;
      margin-top: 40px;
      transition: all .3s;
      &:hover{
        text-decoration: none;
        background: #23527c;
      }
    }
  }
</style>



